$medium: "(max-width: 800px)";

.keyboard {
  display:flex {}
  flex-direction: column;
  height: 100%; }

.keyboard-row {
  display: flex;
  justify-content: space-around;
  height: 33%; }

.drum-pad {
  margin: 4%;
  flex-basis: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  background-color: #FFE5AA;
  box-shadow: black 3px 3px 5px; }
.drum-pad--clicked {
  background-color: #551F00; }

.drum-pad:hover {
  background-color: #AA6239; }

@media screen and #{($medium)} {}
.drum-pad {
  height: 75%; }
